.stars {
  display: flex;

  &__star {
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

    width: 16px;
    height: 16px;
    border-radius: 0.5px;
    margin-right: 4px;
  }

  &--star:first-child {
    margin-left: 0;
  }

  @for $index from 1 through 5 {
    &--#{$index} > :nth-child(-n+#{$index}) {
      background-image: url(/images/star-active.svg);
    }
  }

}
